@import url(https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,700;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}

.profile{height:679px;padding-top:2em}.profile-picture{display:none}.jumbotron{display:flex;flex-direction:column;width:80%;margin-left:3em;height:80%}.modes{display:flex;justify-content:center;align-content:flex-end}.arrow-down{color:white}@media only screen and (min-width: 500px){.modes{padding-top:3em}}@media only screen and (min-width: 1110px){.profile{display:default}}@media only screen and (min-width: 820px){.jumbotron{width:40%}.modes{padding-top:0em}.profile-picture{display:block}}@media only screen and (min-width: 1222px){.modes{padding-top:2em}}@media only screen and (min-height: 632px){.jumbotron{height:80%}}

.Summary{padding:0}.Summary h4{-webkit-animation-name:title;animation-name:title;-webkit-animation-duration:1.5s;animation-duration:1.5s}.Summary p{text-align:justify;-webkit-animation-name:text;animation-name:text;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes title{0%{opacity:0}100%{opacity:100}}@keyframes title{0%{opacity:0}100%{opacity:100}}@-webkit-keyframes text{0%{opacity:0}100%{opacity:100}}@keyframes text{0%{opacity:0}100%{opacity:100}}

.upper-half,.bottom-half{display:flex;justify-content:space-evenly;padding-bottom:4em}.bottom-half{padding-bottom:0em}.front-end,.back-end,.testing,.programming-languages,.soft-skills,.acquiring{line-height:0.5}h5{line-height:2;-webkit-animation-name:skills-titles;animation-name:skills-titles;-webkit-animation-duration:1.5s;animation-duration:1.5s}.skills p{-webkit-animation-name:skills-text;animation-name:skills-text;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes skills-titles{0%{opacity:0}100%{opacity:100}}@keyframes skills-titles{0%{opacity:0}100%{opacity:100}}@-webkit-keyframes skills-text{0%{opacity:0}100%{opacity:100}}@keyframes skills-text{0%{opacity:0}100%{opacity:100}}

.Head{margin:0px;height:679px;background-color:#a68500;background-image:url("https://www.transparenttextures.com/patterns/arches.png")}.links{display:flex;justify-content:flex-end;color:white;padding-top:1em;padding-right:2em}.links a:nth-child(1){color:white;margin-right:1em}.links a:nth-child(2){color:white}.links a:nth-child(3){color:white;margin-left:1em}.Introduction-text{display:flex;flex-direction:column;padding-left:2em;padding-top:3em;font-family:'Archivo', sans-serif;font-size:3em}.name{display:flex;color:white;line-height:2;-webkit-animation-name:name-text;animation-name:name-text;-webkit-animation-duration:1.5s;animation-duration:1.5s}.description{display:flex;flex-direction:column;font-family:'Archivo', sans-serif}.description p{display:none}@-webkit-keyframes name-text{0%{opacity:0}100%{opacity:1}}@keyframes name-text{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes description-one{0%{opacity:0}75%{opacity:0}100%{opacity:1}}@keyframes description-one{0%{opacity:0}75%{opacity:0}100%{opacity:1}}@-webkit-keyframes description-two{0%{opacity:0}90%{opacity:0}95%{opacity:0.5}100%{opacity:1}}@keyframes description-two{0%{opacity:0}90%{opacity:0}95%{opacity:0.5}100%{opacity:1}}@media only screen and (min-width: 650px){.description p:nth-child(2){display:flex;line-height:0.5;color:white;-webkit-animation-name:description-two;animation-name:description-two;-webkit-animation-duration:2s;animation-duration:2s}}@media only screen and (min-width: 1140px){.description p:nth-child(3){display:flex;line-height:0.5;color:white;-webkit-animation-name:description-two;animation-name:description-two;-webkit-animation-duration:3.45s;animation-duration:3.45s}.description p:nth-child(2){line-height:1}}@media only screen and (min-width: 1350px){.description p:nth-child(1){display:flex;line-height:1;color:white;-webkit-animation-name:description-one;animation-name:description-one;-webkit-animation-duration:2s;animation-duration:2s}.description p:nth-child(2){display:none}}

.Projects{height:679px;background-color:#a68500;background-image:url("https://www.transparenttextures.com/patterns/arches.png");padding-top:2em}.Projects h4{margin-top:0.5em;font-family:'Archivo', sans-serif;font-size:2em;color:white}.websites{display:flex;justify-content:center;-webkit-animation-name:website;animation-name:website;-webkit-animation-duration:10s;animation-duration:10s;height:80%}.project-img{-webkit-animation-name:website;animation-name:website;-webkit-animation-duration:2s;animation-duration:2s}.toggle-mode-arrows{color:white}.placeholder{color:white}.projects-text{display:flex;justify-content:center}@-webkit-keyframes website{0%{opacity:0}100%{opacity:1}}@keyframes website{0%{opacity:0}100%{opacity:1}}

.project{width:60%;display:flex;text-align:justify;flex-direction:column;align-content:flex-start;color:white}.project-description{margin-bottom:0.5em;padding-top:1em}.project-img{width:100%}

